import http from './ajax'
import { api } from "./api";

export const login = (data) => {
	return http({
		"url": api.login,
		"method": "post",
		"data": data,
	})
}

export const home = (data) => {
	return http({
		"url": api.home,
		"method": "post",
		"timeout": 30000,
		"data": data,
	})
}

